import styles from './Header.module.css';
import { Link } from 'react-router-dom';

import axios from "axios";
import { useEffect, useState } from "react";

//
import { connect, useSelector } from "react-redux";
import { getEmail, removeEmail } from "../redux/getEmail.js"
import { getStore, removeStore } from "../redux/getStore.js"
import { store } from '../redux/store.js'


//  
// const URL = 'http://localhost:5000'
const mainURL = "http://localhost:3000/"

function Header({ dispatchGetEmail, dispatchGetStore, dispatchRemoveEmail, dispatchRemoveStore }) {
    //   
    const reduxState = useSelector((state) => state);
    const emoji = reduxState.emoji;
    const emoji_url = emoji.filter((item)=> item["name"].includes("Desert Island"))[0]?.url
    

    //     
    const ValToken = window.Kakao.Auth.getAccessToken();

    //           
    useEffect(() => 
    ValToken == null ? setLoginBtn("Login") :  setLoginBtn("Logout")
    , [])

    //    useState.
    const [loginBtn, setLoginBtn] = useState("login")

    // onClick     
    function LoginOrOut() {
        return(
        ValToken == null ? (
            kakaoLogin()
        ) : (
            kakaoLogout()
            )
        )}
    
    //  
    function kakaoLogout() {
        if (!window.Kakao.Auth.getAccessToken()) {
            alert('Not logged in.')
            return
        }
        window.Kakao.Auth.logout(function () {
            alert('logout ok\naccess token -> ' + window.Kakao.Auth.getAccessToken())
            //  .
            setLoginBtn("Login")
            window.location.href = `${mainURL}`
        })
        //redux persist   Email  
        dispatchRemoveEmail();
        //redux persist   store  
        dispatchRemoveStore();
    }
    

    //   
    function kakaoLogin() {
        window.Kakao.Auth.login({
            scope: 'profile_nickname, profile_image', //       ID .
            success: function (response) {
                // console.log(response) //    
                window.Kakao.API.request({ //   
                    url: '/v2/user/me',
                    success: (res) => {
                        const kakao_account = res.kakao_account;
                        const email = kakao_account.email
                        const nickname = kakao_account.profile.nickname
                        const profile_img = kakao_account.profile.profile_image_url
                        
                        // console log 
                        console.log(email);
                        console.log(nickname);
                        console.log(profile_img);
                        //redux store email  .
                        dispatchGetEmail(email);

                        //axios  Backend  .
                        axios.post(
                            `/login`,
                            {
                                email_give: email,
                                name_give: nickname,
                                img_give: profile_img,
                            })
                            .then((res) => {
                                alert("");
                                // history.push("/main/feed");
                            })
                            .catch((error) => {
                                // console.log(error);
                                console.error(error);
                                alert("");
                            });
                        //db member_list store   call
                        getMemberInfo(email);
                        //  
                        setLoginBtn("Logout")
                        
                    },
                    fail: function (error) {
                        alert("");
                    }
                });
                // window.location.href='/' //  
            },
            fail: function (error) {
                console.log(error);
            }
        })
    };
        //member_list  store  
        const getMemberInfo = async (email) => {
            const response = await axios.get(`/login?email_give=${email}`);
            const memberInfo = JSON.parse(response.data.member_info);
            const DBstoreCocktail = memberInfo[0].store
            console.log(DBstoreCocktail);  
            dispatchGetStore(DBstoreCocktail)
        };
        
        //        - Link   onClick .
        function loginfirst(event) {
            if(ValToken == null){
                event.preventDefault();
                alert("  ");
            }
        }
        const linkTop100 = 0;
    return (
        <div>
            <div className={styles.menu_box}>
                <ul>
                    <li>
                        <h1 className={styles.logo}>
                            <Link to="/">
                                <img className={styles.emoji} src={emoji_url} />
                                   </Link></h1>
                    </li>
                    <li>
                        <Link to={`/find:${linkTop100}`}> </Link>
                    </li>
                    <li>
                        <Link to="/home"> </Link>
                    </li>
                    <li>
                        <Link to="/storage" onClick={loginfirst}>  </Link>
                    </li>
                    <li>
                        <div className={styles.login} onClick={LoginOrOut}>
                            <span className={styles.loginText}>{loginBtn}</span>
                            {/* <AccountCircleOutlinedIcon sx={{ fontSize: 30 }} className={styles.loginIcon} /> */}
                        </div>


                    </li>
                </ul>
            </div>
            {/*   */}
            <div
                onClick={() =>
                    window.scrollTo({
                        top: 0,
                        behavior: "smooth",
                    })
                }
                className={styles.scroll}
            >
                <img src="arrow-up-circle.svg"></img></div>
        </div>
    );
}

function mapDispatchToProps(dispatch) {
    return {
        dispatchGetEmail: email => dispatch(getEmail(email)),
        dispatchGetStore: array => dispatch(getStore(array)),
        dispatchRemoveEmail: email => dispatch(removeEmail(email)),
        dispatchRemoveStore: array => dispatch(removeStore(array))
    };
}

export default connect(null, mapDispatchToProps)(Header);